<template>
  <div class="playlist">
    <div class="go-back" @click="$router.go(-1)">
        <i class="fa fa-chevron-left back" aria-hidden="true"></i>
    </div>
    <PlaylistIroduce v-if="playlist" :item="playlist"></PlaylistIroduce>
    <PlaylistDescribe v-if="playlist" :item="playlist"></PlaylistDescribe>
    <hr />
    <ul class="songlist">
      <template v-if="playlist">
        <SongListItem
          v-for="(item, index) in playlist.tracks"
          :key="index"
          :song-item="item"
          :index="index"
          :is-show-quality="false"
         :is-show-ranking="false"
          @translate-currentid="$emit('translate-currentid',$event)"
          :xxx="playlist.tracks"
        ></SongListItem>
      </template>
    </ul>
  </div>
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";
import PlaylistIroduce from "@/components/PlaylistIroduce.vue";
import PlaylistDescribe from "@/components/PlaylistDescribe.vue";


export default {
  data() {
    return {
      playlist: null
    };
  },
  components: {
    SongListItem,
    PlaylistIroduce,
    PlaylistDescribe
  },
  methods: {
    getPlayListDetail(id) {
      this.axios
        .get("http://music.kele8.cn/playlist/detail", {
          params: {
            id: id
          }
        })
        .then(response => {
          ({ playlist: this.playlist } = response.data);
          // this.playlist=response.data.playlist.tracks
          // window.console.log(response.data);
        });
    }
  },
  created() {
    console.log("创建");
  },
  beforeRouteEnter(to, from, next) {
    // window.console.log(to);
    // 路由进入 当前组件实例化之前就 拿数据
    // beforeRouteEnter 守卫 不能 访问 this，因为守卫在导航确认前被调用,因此即将登场的新组件还没被创建
    next(vm => {
      vm.getPlayListDetail(to.query.id);
    });
  }
};
</script>

